import React from 'react';
import styled from 'styled-components';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import CardWrapper from '../components/CardWrapper/CardWrapper.tsx';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 16px;
    padding-bottom: 70px;
    background-color: #f4f2ee;
    @media (max-width: 768px){
        margin-top: 12px
    };
`;

const StyledCardWrapper = styled(CardWrapper)`
    margin-bottom: 20px;
    width: 600px; /* Set a consistent width for all cards */
    margin-bottom: 15px; /* Add margin to create space between cards */
    padding: 20px; /* Add padding within the card */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    @media (max-width: 768px){
        width: fit-content;
        margin-left: 10px;
        margin-right: 10px;
    }
`;

const CardContent = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
`;

const CardHeader = styled.div`
    display: flex;
    align-items: center;
    margin-bottom: 10px;
`;

const CardImage = styled.img`
    width: 50px;
    height: 50px;
    margin-right: 10px;
    border-radius: 50%;
`;

const JobTitle = styled.h3`
    margin: 0;
    font-size: 14px;
    color: #666;
    font-weight: bold;
`;

const JobPeriod = styled.p`
    margin: 0;
    font-size: 14px;
    color: #666;
`;

const Description = styled.div`
    display: flex;
    color: #666;
    margin-bottom: 5px;

    p {
        font-size: 14px;
        margin: 0;
    }

    svg {
        font-size: 10px;
        margin-right: 10px;
        padding-top: 6px;
    }
`;

const Experiences: React.FC<{}> = () => {
    return (
        <Container>
            <StyledCardWrapper id='experience-card-1' bgColor='white' padding='20px'>
                <CardContent>
                    <CardHeader>
                        <CardImage src='demand_side_logo.jpg' alt='Demand Side Logo' />
                        <div>
                            <JobTitle>Full Stack Developer</JobTitle>
                            <JobPeriod>Demand Side Instruments, March 2022 - February 2023</JobPeriod>
                        </div>
                    </CardHeader>                    
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Led the development of web applications, increasing code efficiency by 20% with ReactJS and TypeScript best practices.</p>
                    </Description>                        
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Enhanced location-based services using Google Maps APIs, boosting user engagement by 15%.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Designed responsive UIs with SCSS, improving interface responsiveness by 25%.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Boosted feature deployment speed by 30% in cross-functional teams.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Applied agile methodologies and participated in peer code reviews to ensure coding standards.</p>
                    </Description>    
                </CardContent>
            </StyledCardWrapper>
            <StyledCardWrapper id='experience-card-2' bgColor='white' padding='20px'>
                <CardContent>
                    <CardHeader>
                        <CardImage src='self_employed_logo.jpeg' alt='Logo' />
                        <div>
                            <JobTitle>Front-end Developer</JobTitle>
                            <JobPeriod>Self Employed, November 2020 - February 2022</JobPeriod>
                        </div>
                    </CardHeader>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Enhanced maintainability and scalability with Redux, reducing loading times by 20%.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Integrated RESTful and GraphQL APIs, improving data retrieval times by 30%.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Streamlined development using modern React hooks and functional components, increasing development speed by 40%.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Developed reusable UX components and employed continuous deployment practices to maintain high code quality.</p>
                    </Description>
                </CardContent>
            </StyledCardWrapper>
            <StyledCardWrapper id='experience-card-3' bgColor='white' padding='20px'>
                <CardContent>
                    <CardHeader>
                        <CardImage src='dialogflowsaisolutions_logo.jpg' alt='Dialogflows AI Solutions Logo' />
                        <div>
                            <JobTitle>Software Engineer</JobTitle>
                            <JobPeriod>Dialogflows AI Solutions Inc., June 2020 - October 2020</JobPeriod>
                        </div>
                    </CardHeader>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Boosted application performance by 30% with ReactJS optimizations and reduced bug rates by 25% through rigorous testing.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Facilitated technical communication across departments and developed custom WordPress themes.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Managed Zendesk ticketing system and contributed to agile development processes and peer code reviews.</p>
                    </Description>
                </CardContent>
            </StyledCardWrapper>
            <StyledCardWrapper id='experience-card-4' bgColor='white' padding='20px'>
                <CardContent>
                    <CardHeader>
                        <CardImage src='vardaam_web_solutions_logo.jpg' alt='Vardaam Web Solutions Logo' />
                        <div>
                            <JobTitle>WordPress Developer</JobTitle>
                            <JobPeriod>Vardaam Web Solutions Pvt Ltd, February 2020 - May 2020</JobPeriod>
                        </div>
                    </CardHeader>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Translated wireframes into high-fidelity HTML web pages.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Implemented responsive designs using CSS Flexbox and Grid.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Worked with cross-functional teams to ensure seamless integration and deployment of web solutions.</p>
                    </Description>
                    <Description>
                        <ArrowForwardIosIcon />
                        <p>Conducted unit testing and debugging to enhance code quality and reliability.</p>
                    </Description>
                </CardContent>
            </StyledCardWrapper>
        </Container>
    );
};

export default React.memo(Experiences);